<template><section><h1>PlaneGraphics</h1>
<p><code v-pre>vc-graphics-plane</code> 组件用于加载平面。需要作为 <code v-pre>vc-entity</code> 的子组件使用。</p>
<h2>示例</h2>
<h3>加载平面</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-entity :position="position1" :description="description" :plane.sync="planeEntity1">
          <vc-graphics-plane ref="plane1" :plane="plane1" :dimensions="dimensions1" :material="material1"></vc-graphics-plane>
        </vc-entity>
        <vc-entity :position="position2" :description="description" :plane.sync="planeEntity2">
          <vc-graphics-plane ref="plane2" :plane="plane2" :dimensions="dimensions2" :material="material2" :outline="true" :outlineColor="outlineColor2"></vc-graphics-plane>
        </vc-entity>
        <vc-entity :position="position3" :description="description" :plane.sync="planeEntity3">
          <vc-graphics-plane ref="plane3" :plane="plane3" :dimensions="dimensions3" :fill="false" :outline="true" :outlineColor="outlineColor3"></vc-graphics-plane>
        </vc-entity>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
