        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-plane</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"plane1"</span> <span class="hljs-attr">:plane</span>=<span class="hljs-string">"plane1"</span> <span class="hljs-attr">:dimensions</span>=<span class="hljs-string">"dimensions1"</span> <span class="hljs-attr">:material</span>=<span class="hljs-string">"material1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-plane</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
