        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-plane</span>
          <span class="hljs-attr">ref</span>=<span class="hljs-string">"plane2"</span>
          <span class="hljs-attr">:plane</span>=<span class="hljs-string">"plane2"</span>
          <span class="hljs-attr">:dimensions</span>=<span class="hljs-string">"dimensions2"</span>
          <span class="hljs-attr">:material</span>=<span class="hljs-string">"material2"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
          <span class="hljs-attr">:outlineColor</span>=<span class="hljs-string">"outlineColor2"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-plane</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-entity</span> <span class="hljs-attr">:position</span>=<span class="hljs-string">"position3"</span> <span class="hljs-attr">:description</span>=<span class="hljs-string">"description"</span> <span class="hljs-attr">:plane.sync</span>=<span class="hljs-string">"planeEntity3"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-plane</span>
          <span class="hljs-attr">ref</span>=<span class="hljs-string">"plane3"</span>
          <span class="hljs-attr">:plane</span>=<span class="hljs-string">"plane3"</span>
          <span class="hljs-attr">:dimensions</span>=<span class="hljs-string">"dimensions3"</span>
          <span class="hljs-attr">:fill</span>=<span class="hljs-string">"false"</span>
          <span class="hljs-attr">:outline</span>=<span class="hljs-string">"true"</span>
