<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">description</span>: <span class="hljs-string">'Hello Vue Cesium'</span>,
        <span class="hljs-attr">position1</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">114.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">planeEntity1</span>: {},
        <span class="hljs-attr">plane1</span>: { <span class="hljs-attr">normal</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">1</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">0</span> }, <span class="hljs-attr">distance</span>: <span class="hljs-number">0.0</span> },
        <span class="hljs-attr">dimensions1</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">400000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">material1</span>: <span class="hljs-string">'BLUE'</span>,

        <span class="hljs-attr">position2</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">107.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">planeEntity2</span>: {},
        <span class="hljs-attr">plane2</span>: { <span class="hljs-attr">normal</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">1</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">0</span> }, <span class="hljs-attr">distance</span>: <span class="hljs-number">0.0</span> },
        <span class="hljs-attr">dimensions2</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">400000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">material2</span>: {},
        <span class="hljs-attr">outlineColor2</span>: <span class="hljs-string">'BLACK'</span>,

        <span class="hljs-attr">position3</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">100.0</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">40.0</span>, <span class="hljs-attr">height</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">planeEntity3</span>: {},
        <span class="hljs-attr">plane3</span>: { <span class="hljs-attr">normal</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">0</span>, <span class="hljs-attr">z</span>: <span class="hljs-number">1</span> }, <span class="hljs-attr">distance</span>: <span class="hljs-number">0.0</span> },
        <span class="hljs-attr">dimensions3</span>: { <span class="hljs-attr">x</span>: <span class="hljs-number">400000.0</span>, <span class="hljs-attr">y</span>: <span class="hljs-number">300000.0</span> },
        <span class="hljs-attr">outlineColor3</span>: <span class="hljs-string">'YELLOW'</span>
      }
    },
    mounted() {
      <span class="hljs-built_in">Promise</span>.all([<span class="hljs-keyword">this</span>.$refs.plane1.createPromise, <span class="hljs-keyword">this</span>.$refs.plane2.createPromise, <span class="hljs-keyword">this</span>.$refs.plane3.createPromise]).then(
        <span class="hljs-function">(<span class="hljs-params">instances</span>) =&gt;</span> {
          instances[<span class="hljs-number">0</span>].viewer.zoomTo(instances[<span class="hljs-number">0</span>].viewer.entities)
        }
      )
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = cesiumInstance
        <span class="hljs-keyword">this</span>.material2 = Cesium.Color.RED.withAlpha(<span class="hljs-number">0.5</span>)
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 plane 是否显示。</td>
</tr>
<tr>
<td>plane</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 plane 的法线和距离。 <strong>结构：{ normal: { x: number, y: number, z: number }, distance: number }</strong></td>
</tr>
<tr>
<td>dimensions</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 plane 的宽和高。 <strong>结构：{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>fill</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code> 指定 plane 是否填充材质。</td>
</tr>
<tr>
<td>material</td>
<td>Object|String|Array</td>
<td><code v-pre>'WHITE'</code></td>
<td><code v-pre>optional</code> 指定 plane 的材质。</td>
</tr>
<tr>
<td>outline</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> 指定 plane 是否绘制轮廓线。</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object|String|Array</td>
<td><code v-pre>'BLACK'</code></td>
<td><code v-pre>optional</code> 指定 plane 轮廓线颜色。</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre>1.0</code></td>
<td><code v-pre>optional</code> 指定 plane 轮廓线宽度。</td>
</tr>
<tr>
<td>shadows</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code> 指定 plane 是否投射或接收阴影。 <strong>DISABLED: 0, ENABLED: 1, CAST_ONLY: 2, RECEIVE_ONLY: 3, NUMBER_OF_SHADOW_MODES: 4, RECEIVE_ONLY: 3</strong></td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> 指定 plane 随相机距离改变的显示条件。 <strong>结构：{ near: number, far: number }</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/PlaneGraphics.html">PlaneGraphics</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
<tr>
<td>definitionChanged</td>
<td></td>
<td>每当更改或修改属性或子属性时触发该事件。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          description: 'Hello Vue Cesium',
          position1: { lng: 114.0, lat: 40.0, height: 300000.0 },
          planeEntity1: {},
          plane1: { normal: { x: 1, y: 0, z: 0 }, distance: 0.0 },
          dimensions1: { x: 400000.0, y: 300000.0 },
          material1: 'BLUE',

          position2: { lng: 107.0, lat: 40.0, height: 300000.0 },
          planeEntity2: {},
          plane2: { normal: { x: 0, y: 1, z: 0 }, distance: 0.0 },
          dimensions2: { x: 400000.0, y: 300000.0 },
          material2: {},
          outlineColor2: 'BLACK',

          position3: { lng: 100.0, lat: 40.0, height: 300000.0 },
          planeEntity3: {},
          plane3: { normal: { x: 0, y: 0, z: 1 }, distance: 0.0 },
          dimensions3: { x: 400000.0, y: 300000.0 },
          outlineColor3: 'YELLOW'
        }
      },
      mounted() {
        Promise.all([this.$refs.plane1.createPromise, this.$refs.plane2.createPromise, this.$refs.plane3.createPromise]).then(
          (instances) => {
            instances[0].viewer.zoomTo(instances[0].viewer.entities)
          }
        )
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.material2 = Cesium.Color.RED.withAlpha(0.5)
        }
      }
    }
  </script>